@import '../helpers/_helpers';

.main {
	margin: 0;
	overflow: visible;
}
.slide {
	margin: 0 auto;
	overflow-y: hidden;
	position: relative;
	display: block;
}
.slide_1 {
    background: #BDECB6;
	background-size: 100% 100%;
}
.slide_2 {
    background: #fdfdfd;

}

.slide_3 {
	background: #CD9575;
}
.box {
    width: 800px;
    margin: 0 auto;
	height: 100vh;
	max-width: 1000px;
	display: flex;
	flex-flow: row;
	justify-content: space-between;
	align-items: center;
	line-height: 1.2;
	img {
		display: block;
	}
	@include max-screen(991px) {
		width: auto;
		max-width: 800px;
	}
	@include max-screen(540px) {
		flex-flow: column;
		justify-content: space-around;
		.box2 {
			margin: 0;
		}
	}
}
.slide_3 {
    background: #FFDEAD;
}

.box {
	position: relative;
}
.box  .name {
	font-size: 40px;
	font-family: 'Roboto', arial;
	transform: uppercase;
}
